@font-face {
  font-family: 'Black Ops One';
  font-style: normal;
  font-weight: 400;
  src: local("Black Ops One"), local("BlackOpsOne-Regular"), url("./assets/BlackOpsOne.woff") format("woff"); }

.pp-score_add, .pp-score_remove, .pp-reset_button {
  outline: none; }
  .pp-score_add:active, .pp-score_remove:active, .pp-reset_button:active, .pp-score_add:focus, .pp-score_remove:focus, .pp-reset_button:focus {
    outline: none; }

.pp-container {
  background-color: #fff;
  color: #303030;
  font-family: 'Black Ops One', cursive;
  font-size: 16px;
  width: 100%;
  height: 100%;
  min-width: 100vw;
  min-height: 100vh;
  display: flex;
  flex-flow: column nowrap;
  align-items: stretch;
  align-content: stretch; }

.pp-header {
  background-color: #303030;
  color: #fff;
  height: 2em;
  width: 100%;
  display: flex; }

.pp-header_legend {
  text-align: center;
  margin: auto; }

.pp-header_turn {
  color: #D4AD40; }

.pp-scores {
  display: flex;
  flex-flow: row wrap;
  flex: 1 1 auto;
  width: 100%; }

.pp-score {
  display: flex;
  flex-flow: row wrap;
  font-family: inherit;
  flex: 1 1 0; }
  .pp-score:nth-of-type(1) {
    background-color: #fff;
    color: #303030; }
  .pp-score:nth-of-type(2) {
    background-color: #303030;
    color: #fff; }

.pp-score_number {
  display: block;
  flex: 0 2 100%;
  margin: auto;
  font-family: inherit;
  text-align: center;
  font-size: 25vw;
  color: inherit; }

.pp-score_add, .pp-score_remove {
  flex: 1 1;
  align-self: flex-end;
  margin: 0;
  height: 1.2em;
  line-height: 1.2em;
  font-size: 3em;
  color: #fff;
  border: 0; }
  .pp-score_add:active, .pp-score_remove:active {
    filter: brightness(110%); }

.pp-score_add {
  background-color: #7d7d7d; }

.pp-score_remove {
  background-color: #b0b0b0; }

.pp-reset {
  height: 2em;
  width: 100%; }

.pp-reset_button {
  width: 100%;
  height: 100%;
  background-color: #303030;
  color: #fff;
  margin: 0;
  border: 0; }
  .pp-reset_button:active {
    filter: brightness(110%); }
